              type="number"
              class="inputnumber"
              v-model.number="settings.circle.r"
            >
          </td>
          <td>
            c G
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.g"
            >
          </td>
          <td>
            c B
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.b"
            >
          </td>
        </tr>
        <tr>
          <td>
            MinSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.minSize"
            >
          </td>
          <td>
            MaxSize
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.circle.maxSize"
            >
          </td>
          <td>FPS: {{fps}}&nbsp;</td>
