        <tr>
          <td>
            cellWidth
            <input
              min="5"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellWidth"
            >
          </td>
          <td>
            cellHeight
            <input
              min="5"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.grid.cellHeight"
            >
          </td>
          <td>
            Speed
            <input
              min="0"
              max="255"
              type="number"
              class="inputnumber"
              v-model.number="settings.speed"
            >
          </td>
        </tr>
        <tr>
          <td colspan="4">
            Anim
            <input
              min="1"
              max="3"
              type="number"
              class="inputnumber"
              v-model.number="settings.anim"
            >
          </td>
        </tr>
      </table>
    </div>

    <!--<audio width="200" height="200" id="colorqsuad" crossorigin="anonymous" loop autoplay controls>-->
      <!--<source src="colorqsuad2.mp3" type="audio/mp3">-->
      <!--You need an MP3-supporting browser.-->
