  </div>
</template>

<script>
import * as PIXI from 'pixi.js'
import tinyColor from 'tinycolor2'

export default {
  name: 'app',

  data () {
    return {
      settings: {
        anim: 3, // 1 = spectrum, 2 = EQ, 3 = waveform
        debug: true,
        controls: true,
        speed: 14,
        bg: {
          r: 30,
          g: 0,
          b: 0
        },
        grid: {
          cellWidth: 30,
          cellHeight: 30
        },
        circle: {
          r: 222,
          g: 53,
          b: 72,
          maxSize: 7,
          minSize: 0
        }
      },
      grid: {
        cols: 0,
        rows: 0,
        width: 0,
        height: 0
      },
      layout: {
        width: window.innerWidth,
        height: window.innerHeight
      },
      fps: null,
      audio: {
        setupDone: false,
        selectedChannels: [],
        bufferLength: null,
        data: [], // levels by freq
        timeData: [] // waveform data
      }
    }
  },

  mounted () {
    window.PIXI = PIXI
    this.tlog = this.throttle(this.log, 500)
    this.setupAudio()
    this.setup()
  },

  watch: {
    settings: {
      handler () {
        this.log('settings change')
        this.debounceResize()
      },
      deep: true
    }
  },

  methods: {

    setup () {
      this.log('setup')
      this.setupVars()
      this.setupApp()
      this.setAudioAnim()
    },

    setupVars () {
      this.log('setupVars')
      this.p = {
        t1: (performance || Date).now(),
        t2: null,
        circles: [],
        audioNode: null,
        AudioContext: null,
        analyser: null,
        removableListener: null
      }
    },

    setupApp () {
      this.log('setupApp')

      // app creation
      this.p.app = new PIXI.Application({
        antialias: true,
        width: this.layout.width,
        height: this.layout.height,
        backgroundColor: this.rgbToHex(this.settings.bg.r, this.settings.bg.g, this.settings.bg.b),
        resolution: window.devicePixelRatio || 1
      })

      this.log('window pixelratio: ', window.devicePixelRatio)
      document.body.appendChild(this.p.app.view)
      this.log('app: ', this.p.app)
      window.app = this.p.app

      this.p.app.renderer.plugins.interaction.on('pointerup', this.toggleAudio)

      // texture creation
      this.p.svg = PIXI.Texture.from('circle.svg')

      // boot after a while to allow resources loading
      requestAnimationFrame(() => {
        this.log('requestAnimationFrame before app start')
        this.p.app.ticker.speed = this.settings.speed * 100

        this.setupGrid(this.layout.width, this.layout.height)

        this.p.app.ticker.add((delta) => {
          // use delta to create frame-independent transform
          // eg. container.rotation -= 0.01 * delta
          this.animate(delta)
        })
        window.addEventListener('resize', this.debounceResize)
      })
    },

    circle (x, y, size) {
      // this.log('circle')
      let circle = new PIXI.Sprite(this.p.svg)
      // circle.tint = Math.random() * 0xFFFFFF
      circle.tint = this.rgbToHex(this.settings.circle.r, this.settings.circle.g, this.settings.circle.b)
      circle.tintSave = circle.tint
      // circle.audioTint = tinyColor.random().toHexString() // of type #00ff00
      // circle.audioTint = tinyColor.random().toHex() // of type 00ff00
      circle.audioTint = '21cab7'
      // circle.audioTint = this.rgbToHex2(this.settings.circle.r, this.settings.circle.g, this.settings.circle.b)
      circle.audioTintSave = circle.audioTint
      circle.audioBoost = false
      circle.anchor.set(0.5)
      circle.x = x
      circle.y = y
      circle.scale.x = size / 100
      circle.scale.y = size / 100
      circle.cycles = 0

      // this.log('TINT: ', circle.tint)

      circle.size = size
      circle.grow = size > this.settings.circle.minSize && size < this.settings.circle.maxSize ? Math.random() < 0.5 : !size

      // this.log('circle init: ', circle.grow, circle)
      return circle
    },

    animate (delta) {
      // this.log('animate: ', this.p.app.ticker.maxFPS, this.p.app.ticker.FPS)
      // halve the audio repaints
      let repaint = this.settings.anim === 2
      // if (Math.random() < 0.1) {
      //   repaint = true
      // }
      // repaint = true
      this.p.t2 = (performance || Date).now()
      if (this.p.t2 >= this.p.t1 + 100) {
        this.fps = Math.round(this.p.app.ticker.FPS)
        this.p.t1 = this.p.t2
        repaint = true
      }

      let audioVals = []
      let audioData = []
      if (this.audio.setupDone && this.playing) {
        if (this.settings.anim === 3) {
          this.analyser.getByteTimeDomainData(this.audio.data)
        } else {
          this.analyser.getByteFrequencyData(this.audio.data)
        }


        // trimmed audioData (extremes not pretty)
        if (this.settings.anim !== 3) {
          audioData = this.audio.data.slice(this.audio.bufferLength / 10, this.audio.bufferLength - this.audio.bufferLength / 4)
          this.tlog('audioData: ', audioData.length, this.audio.data.length)
        } else {
          audioData = this.audio.data
        }
        // reduce audio data to grid col (averaging values)
        const divider = audioData.length / this.grid.cols

        for (let i = 0; i < this.grid.cols; i++) {
          let vals = []

          audioData.forEach((data, j) => {
            let v = Math.ceil(j / divider)

            if (v === i) {
              vals.push(data)
            }
          })

          // averaged audio values
          audioVals[i] = vals.length ? vals.reduce((tot, val) => tot + val) / vals.length : 0
        }
      }

      this.p.app.stage.children.forEach((circle) => {
        // circle.scale.x += 0.001
        const size = circle.scale.x
        const rand = Math.random() < 0.5

        const hasSize = circle.scale.x > 0
        circle.grow = size > this.settings.circle.minSize / 100 && size < this.settings.circle.maxSize / 100 ? circle.grow : !hasSize

        let step = circle.grow ? 0.001 : -0.001

        // dice added move
        if (rand) {
          step += circle.grow ? 0.0005 : -0.0005
        }

        circle.scale.x = ((circle.scale.x * 1000 + step * delta) / 1000)
        circle.scale.y = ((circle.scale.y * 1000 + step * delta) / 1000)

        // audio colors
        if (this.audio.setupDone && repaint) {
          let audioValue = 0

          if (this.settings.anim === 3) {
            audioValue = audioVals[circle.col]
            this.animAudioSine(circle, audioValue)
          } else if (this.settings.anim === 2) {
            audioValue = audioVals[circle.col]
            this.animAudioEq(circle, audioValue)
          } else {
            audioValue = audioVals[circle.col]
            this.animAudioVariance(circle, audioValue)
          }
        }
      })
    },

    animAudioSine (circle, audioValue) {
      // looking if circle.row is the one to be highlighted
      let val = Math.round((audioValue * this.grid.rows) / 256)

      if ([circle.row, circle.row - 1, circle.row + 1].indexOf(val) > -1 && this.playing) {
      // if (circle.row === val && this.playing) {
        // console.log(val)
        circle.tint = 0xffffff
        // circle.tint = '0x' + circle.audioTint
        // circle.tint = this.colorLuminance(circle.tint, 0.8)
      } else if (circle.tint !== circle.tintSave) {
        if (circle.tint !== '0x050505') {
          circle.tint = this.colorLuminance(circle.tint, -0.1)
        } else {
          circle.tint = circle.tintSave
        }
      }
    },

    animAudioEq (circle, audioValue) {
      // looking if circle.row is the one to be highlighted
      let val = Math.round((audioValue * this.grid.rows) / 256)

      if (circle.row === (this.grid.rows - val) && this.playing) {
        circle.tint = 0xffffff
      } else if (circle.tint !== circle.tintSave) {
        if (circle.tint !== '0x050505') {
          circle.tint = this.colorLuminance(circle.tint, -0.1)
        } else {
          circle.tint = circle.tintSave
        }
      }
    },

    animAudioVariance (circle, audioValue) {
      // looking if circle.row is the one to be highlighted
      let val = Math.round((audioValue * this.grid.rows) / 256)

      let newTint = null

      if (circle.row === (this.grid.rows - val) && this.playing) {
        newTint = 0xffffff
      } else if (!audioValue || !circle.lastAudio) { // init or 0 audioValue
        if (circle.tint !== circle.tintSave) {
          newTint = circle.tintSave
          circle.audioTint = circle.audioTintSave
        }
      } else { // audio modifications
        if (circle.lastAudio < audioValue) { // decrease
          if (circle.tint !== circle.tintSave) {
            newTint = circle.tintSave
            circle.audioTint = circle.audioTintSave
          }
        } else if (circle.lastAudio === audioValue) { // equals
          if (circle.tint !== circle.tintSave) {
            newTint = circle.tintSave
            circle.audioTint = circle.audioTintSave
          }
        } else if (circle.row === val) {
          // newTint = 0xffffff
        } else { // increase
          // if (rand) {
          //   circle.audioTint = tinyColor(circle.audioTint).brighten(6).saturate(5).spin(2).toHex()
          // } else {
          //   // circle.audioTint = tinyColor(circle.audioTint).darken(5).desaturate(5).toHex()
          //   circle.audioTint = tinyColor(circle.audioTint).darken(5).desaturate(5).toHex()
          // }
          // newTint = this.colorLuminance(circle.tint, -0.2)
          newTint = this.colorLuminance(circle.audioTint, -0.4)
          circle.audioTint = newTint
          // newTint = '0x' + circle.audioTint
          // newTint = circle.audioTint
          // newTint = 0xffffff
        }
      }

      if (newTint) {
        circle.tint = newTint
      }

      circle.lastAudio = audioValue
    },

    setupGrid (width, height) {
      this.log('setupGrid: ', width, height)
      this.grid.width = width
      this.grid.height = height

      this.grid.cols = Math.floor(width / this.settings.grid.cellWidth)
      this.grid.rows = Math.floor(height / this.settings.grid.cellHeight)
      this.log('grid cols, rows: ', this.grid.cols, this.grid.rows)

      // this.audio.selectedChannels = this.split(0, this.audio.bufferLength - 1, this.grid.rows)
      // this.grid.rowSplit = this.split(0, this.grid.rows - 1)
      // this.log('split buffer, rows, split: ', this.audio.bufferLength, this.grid.rows, this.audio.selectedChannels)

      for (let i = 0; i < this.grid.cols; i++) {
        for (let j = 0; j < this.grid.rows; j++) {
          const size = Math.floor(Math.random() * (this.settings.circle.maxSize - this.settings.circle.minSize + 1)) + this.settings.circle.minSize

          const x = i * this.settings.grid.cellWidth + this.settings.grid.cellWidth / 2
          const y = j * this.settings.grid.cellHeight + this.settings.grid.cellHeight / 2

          const circle = this.circle(x, y, size)
          circle.col = i
          circle.row = j
          this.p.app.stage.addChild(circle)
        }
      }

      this.setAudioAnim()
    },

    toggleAudio () {
      this.log('toggleAudio')
      if (!this.alreadyPlayed) {
        this.playAudio()
      } else {
        this.playing ? this.pauseAudio() : this.playAudio()
      }
    },

    playAudio () {
      this.log('playAudio')
      this.alreadyPlayed = true
      this.playing = true
      this.startedAt = this.pausedAt ? Date.now() - this.pausedAt : Date.now()
      this.asource = null
      this.asource = this.actx.createBufferSource()
      this.asource.buffer = this.audioBuffer
      this.asource.loop = true
      this.asource.connect(this.gainNode)
      this.pausedAt ? this.asource.start(0, this.pausedAt / 1000) : this.asource.start()
    },

    pauseAudio () {
      this.log('pauseAudio')
      this.playing = false
      this.pausedAt = Date.now() - this.startedAt
      this.asource.stop()
    },

    setupAudio () {
      if (this.audio.setupDone) {
        return
      }

      if (!this.actx) {
        this.actx = new (window.AudioContext || window.webkitAudioContext)()
        if (!this.actx) {
          this.log('Web Audio API not supported :(')
          return
        }
      }

      var xmlHTTP = new XMLHttpRequest()

      this.log('- Loading Audio Buffer -')

      // xmlHTTP.open('GET', 'https://spoutnikz.github.io/colorqsuad.mp3', true)
      xmlHTTP.open('GET', 'colorqsuad.mp3', true)
      xmlHTTP.responseType = 'arraybuffer'

      xmlHTTP.onload = (e) => {
        this.log('- Decoding Audio File Data -')
        this.analyser = this.actx.createAnalyser()
        // this.analyser.fftSize = 128
        // this.analyser.minDecibels = -40
        // this.analyser.maxDecibels = 0
        this.analyser.smoothingTimeConstant = 0.8
        // this.analyser.smoothingTimeConstant = 0

        this.actx.decodeAudioData(e.currentTarget.response, (buffer) => {
          this.log('decoding audio data, ready')

          this.audioBuffer = buffer
          this.gainNode = this.actx.createGain()

          this.gainNode.connect(this.analyser)
          this.analyser.connect(this.actx.destination)

          this.log('setupDone, typeof audio.data: ', typeof this.audio.data)
          this.setAudioAnim()
          this.playAudio()
        }, (e) => {
          this.log('Error decoding audio data: ', e.err)
        })
      }

      xmlHTTP.send()
    },

    setAudioAnim () {
      this.log('setAudioAnim')
      if (!this.analyser) {
        this.log('setAudioAnim analyser not ready')
        return
      }

      const val = this.nearestPow(this.grid.cols * 3, 2)
      this.log('setAudioAnim val, cols, cols*3: ', val, this.grid.cols, this.grid.cols * 3)

      if (this.settings.anim === 3) {
        // this.analyser.fftSize = this.grid.cols * 2 >= val ? val * 2 : val
        // this.analyser.fftSize = 8192
        this.analyser.fftSize = 4096
        // this.analyser.fftSize = 32768
      } else if (this.settings.anim === 2) {
        this.analyser.fftSize = this.grid.cols * 2 >= val ? val * 2 : val
        this.log('fftSize: ', this.analyser.fftSize)
      } else {
        // this.analyser.fftSize = this.grid.cols * 2 >= val ? val * 2 : val
        this.analyser.fftSize = val * 2
      }
      this.audio.bufferLength = this.analyser.frequencyBinCount
      this.audio.data = new Uint8Array(this.audio.bufferLength)
      this.log('audio.bufferLength: ', this.audio.bufferLength)
      this.audio.setupDone = true
    },

    resize () {
      this.log('resize')
      this.layout.width = window.innerWidth
      this.layout.height = window.innerHeight

      this.destroy()
      this.setup()
    },

    debounceResize () {
      window.clearTimeout(window.resizeTimeout)
      window.resizeTimeout = window.setTimeout(this.resize, 100)
    },

    destroy () {
      this.log('destroy')
      window.removeEventListener('resize', this.debounceResize)
      window.clearTimeout(window.resizeTimeout)

      if (this.p.app && this.p.app.destroy) {
        this.log('app destroy')
        this.p.app.destroy(true, true)
      }

      if (this.source) {
        this.source.disconnect()
      }

      if (this.p.analyser && this.p.analyser.disconnect) {
        this.p.analyser.disconnect()
      }

      this.p = null
      window.app = null
    },

    nearestPow (value, pow) {
      return Math.pow(pow, Math.round(Math.log(value) / Math.log(pow)))
    },

    rgbToHex (r, g, b) {
      return '0x' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)
    },

    rgbToHex2 (r, g, b) {
      return ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1)
    },

    // lum range [-1, 1]
    colorLuminance (hex, lum) {
      // validate hex string
      hex = String(hex).replace(/^(0x)/gi, '')
      if (hex.length < 6) {
        hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2]
      }
      lum = lum || 0

      // convert to decimal and change luminosity
      var rgb = '0x'; var c; var i
      for (i = 0; i < 3; i++) {
        c = parseInt(hex.substr(i * 2, 2), 16)
        c = Math.round(Math.min(Math.max(0, c + (c * lum)), 255)).toString(16)
        rgb += ('00' + c).substr(c.length)
      }

      // this.log(rgb)

      return rgb
    },

    split (left, right, parts) {
      var result = []
      if (!parts) {
        return result
      }
      var delta = (right - left) / (parts - 1)
      while (left < right) {
        result.push(Math.round(left))
        left += delta
      }
      result.push(Math.round(right))
      return result
    },

    throttle (fn, threshhold, context) {
      threshhold = threshhold || 250
      context = context || this

      let last = false

      return function () {
        let args = arguments
        let now = Date.now()

        if (last && now < last + threshhold) return

        last = now
        fn.apply(context, args)
      }
    },

    log () {
      if (this.settings.debug) {
        console.log('[APP log] : ', ...arguments)
      }
    }
  }
}
</script>

